.navigation {
  display: flex;
  flex-direction: column;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navigation__item {
  display: flex;
  justify-content: center;
  padding: 28px 0 26px;
  box-shadow: 0 1px 0 $gray-light, inset 0 1px 0 $gray-light;
}

.navigation__link {
  @include title;
}
